<template>
  <div class="main-height">
    <el-card :body-style="{ padding: '0px' }" v-for="item in videos" :key="item.id">
      <el-row>
        <img :src="item.coverUri" class="image">
        <div style="padding: 14px;">
          <span>{{ item.name }}</span>
        </div>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import { ref } from "vue";
import { getAllVideo } from "../ecm-api";

export default {
  name: "VideoPage",
  data() {
    return {
      videos: ref([]),
    };
  },
  mounted() {
    getAllVideo().then((res) => {
      if (res.code == 0) {
        this.videos = res.data.data;
      }
    });
  },
  methods: {},
};
</script>

<style scoped>
.main-height {
  height: calc(100vh - 210px);
  overflow-y: auto;
}
</style>
